@import 'variables';

.third-collage {
    width: 100%;
    height: 100vh;
    margin-top: 300px;
}

.third-collage-images {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
    

}

.third-collage-image-1 {
    background-image: url(https://ableton-production.imgix.net/about/photo-6-a.jpg?fit=crop&h=400&ixjsv=1.1.3&w=533);
    width: 28.333vw;
    height: 20vw;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    z-index: 1;
   
    
}
.third-collage-image-2 {
    background-image: url(https://ableton-production.imgix.net/about/photo-7.jpg?fit=crop&h=667&ixjsv=1.1.3&w=667);
    width: 38.666vw;
    height: 36.666vw;
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    z-index: 1;
    
}

.color-box-3 {
    position: absolute;
    background-color: rgb(213,179,255);
    height: 100vh;
    width: 80%;
    z-index: 0;
}

@media (max-width:1300px) {
    .color-box-3 {
        height: 70%;
    }
    .third-collage {
        margin-top: 0px;
    }
}

@media(max-width:500px) {
    .third-collage {
        height: 50vh;
    }

    .color-box-3 {
        height: 60%;
    }
    .third-collage-images {
        height: 100%;
    }
}